<template>
  <div id="app"
    v-loading="loading"
    element-loading-text="Jser 正在搬运数据..."
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)">
    <el-container>
      <el-header style="height:auto">
        <PageHeader></PageHeader>
      </el-header>
      <el-main style="padding: 0 20px">
        <MainList :history-key="historyKey" @getDataUrl="getDataUrl"></MainList>
        <img v-show="logo" src="./assets/neau_campus_logo.svg" alt="neau_campus_logo" class="neau-campus-logo">
        <div v-show="logo" class="place-holder">
          Jser 还没有找到数据
        </div>
        <List
          :data-to-transfer="dataToTransfer"
          :server-url="serverUrl"
          @newData="newData"></List>
      </el-main>
      <el-footer>
        <img src="./assets/jser_logo.svg" alt="jser-logo" class="jser-logo">
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import PageHeader from './components/page_header.vue'
import MainList from './components/main_list.vue'
import List from './components/list.vue'
export default {
  name: 'App',
  components: {
    PageHeader,
    MainList,
    List
  },
  data () {
    return {
      url: [],
      serverUrl: '',
      dataToTransfer: [],
      loading: false,
      logo: true,
      newdata: [],
      historyKey: ''
    }
  },
  methods: {
    async getDataUrl (...url) {
      if (url) {
        this.logo = false
        this.loading = true
        this.serverUrl = url[0]
        // eslint-disable-next-line
        const response = await axios.get(url[0] + '?skip=0')
        this.dataToTransfer = response.data
        console.log(this.dataToTransfer)
        this.loading = false
      }
    },
    newData (newdata) {
      if (typeof newdata[0] !== 'string') {
        this.dataToTransfer.push(...newdata)
      }
    }
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.el-header {
  padding: 30px;
  background: linear-gradient(to bottom,#409EFF,white);
}
.el-container {
  height: 100vh;
}
.el-main {
  padding: 0 20px;
  width: 100%;
  height: 100%;
}
.neau-campus-logo {
  height: 30%;
  width: 30%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.5;
}
.place-holder {
  color: rgba(164, 164, 164, 0.5);
  position: absolute;
  left: 50%;
  top: 65%;
  transform: translate(-50%, -65%);
}
.jser-logo {
  padding: 10px;
  width: 70px;
  height: 35px;
}
</style>



