<template>
<div id="detail">
  <el-container>
    <el-header style="height: auto">
      <PageHeader></PageHeader>
    </el-header>
    <el-main>
      <Content :content="content"
        :set-carousel-display="setCarouselDisplay"
        :imgs="imgs"
        :pdf="pdf"
        :text="text"
        :pdf-src="pdfSrc"></Content>
    </el-main>
    <el-footer>
      <img src="./assets/jser_logo.svg" alt="jser_logo" class="jser-logo">
    </el-footer>
  </el-container>
</div>
</template>
<script>
import PageHeader from './components/page_header.vue'
import Content from './components/content.vue'
export default {
  name: 'DetailPage',
  components: {
    PageHeader,
    Content
  },
  data () {
    return {
      fontConfigger: '',
      setCarouselDisplay: '',
      content: [],
      imgs: [],
      text: [],
      pdf: false,
      pdfSrc: ''
    }
  },
  methods: {
    uselessKiller () {
      let text = this.content.init.text
      let imgs = this.content.init.imgs
      let spliceArr = []
      text.forEach(function (val, index) {
        imgs.forEach(function (v, i) {
          if (val === v.title) {
            spliceArr.push(index)
          }
        })
        if (val === '' || val === ' ') {
          spliceArr.push(index)
        }
        if (index === text.length - 1) {
          spliceArr.sort(function (a, b) {
            return a < b ? 1 : -1
          }).forEach(function (value) {
            text.splice(value, 1)
          })
        }
      })
    }
  },
  async mounted () {
    // eslint-disable-next-line
    const result = await axios.post('/getdetail', this.$route.query)
    this.content = result.data[0]
    this.imgs = this.content.init.imgs
    this.text = this.content.init.text
    if (this.$route.query.class.startsWith('neau')) {
      this.pdf = false
    } else {
      if (this.content.init.pdf.length !== 0) {
        this.pdf = true
        this.pdfSrc = this.content.init.pdf[0]
      }
    }
    this.uselessKiller()
    if (this.content.init.imgs.length === 0) {
      this.setCarouselDisplay = 'display: none'
    } else {
      this.setCarouselDisplay = ''
    }
  }
}
</script>
<style>
* {
  margin: 0;
  padding: 0;
}
#detail {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
}
.el-header {
  /* border: 1px solid #000; */
  padding: 30px;
  height: 30vh;
  background: linear-gradient(to bottom,#409EFF,white);
}
.el-container {
  height: 100vh;
}
.el-main {
  padding-top: 0;
}
.jser-logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  width: 70px;
  height: 35px;
}
</style>



