<template>
<div class="list-container">
  <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
    <el-submenu index="1" class="selecter" style="min-width: 0">
      <template slot="title">{{jwcSelecter}}</template>
      <el-menu-item index="教务新闻" style="min-width: 0">教务新闻</el-menu-item>
      <el-menu-item index="教务通知" style="min-width: 0">教务通知</el-menu-item>
      <el-menu-item index="考务通知" style="min-width: 0">考务通知</el-menu-item>
    </el-submenu>
    <el-submenu index="2" class="selecter" style="min-width: 0">
      <template slot="title">{{neauSelecter}}</template>
      <el-menu-item index="学校要闻" style="min-width: 0">学校要闻</el-menu-item>
      <el-menu-item index="通知公告" style="min-width: 0">通知公告</el-menu-item>
      <el-menu-item index="招生动态" style="min-width: 0">招生动态</el-menu-item>
    </el-submenu>
  </el-menu>
</div>
</template>
<style>
.list-container {
  width: 100%;
}
.selecter {
  width: 50%;
}
.el-menu {
  width: 100%;
}
.el-submenu .el-menu-item {
  min-width: 0;
}
</style>

<script>
export default {
  name: 'main-list',
  props: ['historyKey'],
  data () {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      headerText: '',
      jwcSelecter: '教务资讯',
      neauSelecter: '东农资讯',
      requestUrl: '',
      returnKey: ''
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      if (keyPath[0] === '1') {
        this.neauSelecter = '东农资讯'
        this.jwcSelecter = key
        if (key === '教务新闻') {
          this.requestUrl = '/jwc/news'
        } else if (key === '教务通知') {
          this.requestUrl = '/jwc/notices'
        } else if (key === '考务通知') {
          this.requestUrl = '/jwc/exam'
        }
      } else {
        this.jwcSelecter = '教务资讯'
        this.neauSelecter = key
        if (key === '学校要闻') {
          this.requestUrl = '/neau/news'
        } else if (key === '通知公告') {
          this.requestUrl = '/neau/notices'
        } else if (key === '招生动态') {
          this.requestUrl = '/neau/enrollment'
        }
      }
      this.$emit('getDataUrl', this.requestUrl)
    }
  }
}
</script>



