import Vue from 'vue'
import Router from 'vue-router'
import App from '../App'
import DetailPage from '../DetailPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: App
    },
    {
      path: '/detail',
      component: DetailPage
    }
  ]
})



