<template>
<div>
  <h2 class="content-title">{{content.title}}</h2>
  <div class="content-url">原文链接：{{content.url}}</div>
  <el-carousel indicator-position="outside" :interval="3000" arrow="always" height="200px" autoplay :style="setCarouselDisplay">
    <el-carousel-item v-for="img in imgs" :key="img.src">
      <img v-bind:src="img.src" alt="error-image" class="content-imgs">
    </el-carousel-item>
  </el-carousel>
  <section class="content-text">
     <!-- eslint-disable-next-line -->
    <p v-for="item in text">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item}}</p>
  </section>
  <div v-show="pdf" class="pdf-src">PDF文件暂时无法查看，请移步下面的链接查看PDF文件<br>{{pdfSrc}}</div>
</div>
</template>
<script>
/* eslint-disable */
export default {
  name: 'contentPage',
  props: [ "content", "setCarouselDisplay", "pdf", "imgs", "text", "pdfSrc" ]
}
</script>
<style>
.content-title, .content-url {
  padding-bottom: 10px;
}
.content-title {
  font-size: 19px;
}
.content-url {
  color: #cccccc;
}
.content-text, .pdf-src {
  color: #666666;
  font-size: 16px;
}
.pdf-src {
  /* word-break: normal; */
  word-wrap: break-word;
  width: 100%;
}
.content-imgs {
  height: 200px;
  width: 100%;
}
.el-carousel__item img {
  margin: 0;
}
</style>



