<template>
  <div class="table-container">
    <ul
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="70"
      infinite-scroll-immediate-check="check">
      <li class="list-row" v-for="(item, itemIndex) in dataToTransfer" :index="itemIndex" :key="item.url">
        <div class="date">{{item.createTime}}</div>
        <a class="item-title" @click="computeHref(itemIndex)">{{item.title}}</a>
      </li>
    </ul>
  </div>
</template>
<style>
.table-container {
  width: 100%;
  height: 85%;
  overflow: inherit;
  box-shadow: #666666;
}
.list-row {
  width: 100%;
  height: 32px;
  border-bottom: 1px solid #e6e6e6;
  display: flex;
  justify-content: space-around;
}
.date, .item-title {
  line-height: 32px;
  color: #666666;
  text-align: left;
}
.date {
  color: #aaaaaa;
}
.item-title {
  font-size: 15px;
  text-decoration: none;
  cursor: pointer;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 200px;
}
</style>
<script>
import { Indicator } from 'mint-ui'
export default {
  name: 'list',
  props: [ 'dataToTransfer', 'serverUrl' ],
  data () {
    return {
      detailPage: '',
      flag: 0,
      dataToShow: [],
      loading: false,
      check: false
    }
  },
  methods: {
    computeHref (itemIndex) {
      this.detailPage = '#/detail?class=' + this.serverUrl.replace(/\//g, '') + '&id=' + this.dataToTransfer[itemIndex]._id
      location.href = this.detailPage
    },
    async loadMore () {
      this.loading = true
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      setTimeout(async () => {
        this.flag += 20
        // eslint-disable-next-line
        const newData = await axios.get(this.serverUrl + '?skip=' + this.flag)
        this.$emit('newData', newData.data)
        this.loading = false
        Indicator.close()
      }, 2000)
    }
  }
}
</script>



